"use client"
import { useState } from 'react'
import { getZodiacFromDate, getCompatibility } from '../../lib/zodiac'
import ZodiacAvatar from '../../components/ZodiacAvatar'

export default function SoulMatch(){
  const [yourDob, setYourDob] = useState('')
  const [partnerDob, setPartnerDob] = useState('')
  const [partnerSign, setPartnerSign] = useState('')
  const [result, setResult] = useState<any>(null)

  const SIGN_ELEMENT: Record<string, 'Fire'|'Earth'|'Air'|'Water'> = {
    Aries: 'Fire',
    Taurus: 'Earth',
    Gemini: 'Air',
    Cancer: 'Water',
    Leo: 'Fire',
    Virgo: 'Earth',
    Libra: 'Air',
    Scorpio: 'Water',
    Sagittarius: 'Fire',
    Capricorn: 'Earth',
    Aquarius: 'Air',
    Pisces: 'Water',
  }

  function handleCheck(e: React.FormEvent){
    e.preventDefault()
    let yours = yourDob ? getZodiacFromDate(new Date(yourDob)) : null
    let partner: any = partnerDob ? getZodiacFromDate(new Date(partnerDob)) : null
    if(partnerSign) partner = { name: partnerSign, element: SIGN_ELEMENT[partnerSign] }
    if(!yours || !partner){
      setResult({ error: 'Please provide both DOBs or pick a partner sign.' })
      return
    }
    setResult(getCompatibility(yours.name, partner.name))
  }

  return (
    <div className="max-w-3xl w-full card-fade">
      <h2 className="text-2xl font-semibold text-[#3b0764]">SoulMatch — Simple Compatibility</h2>
      <p className="soft-microcopy">Try a partner DOB or pick their sign manually.</p>

      <form className="mt-4 space-y-3" onSubmit={handleCheck}>
        <div>
          <label className="block text-sm">Your DOB</label>
          <input type="date" value={yourDob} onChange={e=>setYourDob(e.target.value)} className="mt-1 rounded-md p-2 w-full" />
        </div>

        <div>
          <label className="block text-sm">Partner DOB</label>
          <input type="date" value={partnerDob} onChange={e=>setPartnerDob(e.target.value)} className="mt-1 rounded-md p-2 w-full" />
        </div>

        <div>
          <label className="block text-sm">Or select partner sign</label>
          <select value={partnerSign} onChange={e=>setPartnerSign(e.target.value)} className="mt-1 rounded-md p-2 w-full">
            <option value="">-- pick --</option>
            {['Aries','Taurus','Gemini','Cancer','Leo','Virgo','Libra','Scorpio','Sagittarius','Capricorn','Aquarius','Pisces'].map(s => (
              <option key={s} value={s}>{s}</option>
            ))}
          </select>
        </div>

        <div className="flex items-center gap-4">
          <button type="submit" className="large-cta">Check Compatibility</button>
        </div>
      </form>

      {result ? (
        <div className="mt-4 p-4 rounded bg-white/90">
          {result.error ? (
            <p className="text-sm text-red-600">{result.error}</p>
          ) : (
            <>
              <h3 className="font-semibold">Summary: {result.summary}</h3>
              <p className="mt-2">Strengths: {result.strengths}</p>
              <p className="mt-2">Conflict Triggers: {result.conflicts}</p>
              <ol className="mt-3 list-decimal list-inside text-sm">
                <li>Listen first — hold curiosity</li>
                <li>Practice one small routine together</li>
                <li>Check in about needs weekly</li>
              </ol>
            </>
          )}
        </div>
      ) : null}
    </div>
  )
}
